                        // let res = await axios.get("/categories");

this.categories = res.data;
          },
          getRandomBG(){
            // var x = Math.floor(Math.random() * 256);
            return `90deg, rgba(${Math.floor(Math.random() * 256)},${Math.floor(Math.random() * 256)},${Math.floor(Math.random() * 256)},1) 0%,
